import { AfterViewInit, ChangeDetectionStrategy, Component, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';

import { FxchScrollDetectorDirective } from '@core-directives/fxch-scroll-detector/fxch-scroll-detector.directive';
import { FxchScrollableContainerDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-container.directive';
import { FxchScrollableHorizontalItemDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-horizontal-item.directive';
import { FxchScrollableDirective } from '@core-directives/fxch-scrollable/fxch-scrollable.directive';
import { FxchScrollableTargetDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-target.directive';
import { FxchScrollableApi } from '@core-directives/fxch-scrollable/models/fxch-scrollable-api';

@Component({
    selector: 'partners',
    standalone: true,
    imports: [
        CommonModule,
        TranslateModule,
        FxchScrollableTargetDirective,
        FxchScrollDetectorDirective,
        FxchScrollableContainerDirective,
        FxchScrollableHorizontalItemDirective,
        FxchScrollableDirective,
    ],
    templateUrl: './partners.component.html',
    styleUrl: './partners.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PartnersComponent implements AfterViewInit, OnDestroy {

    public readonly columnGap = 80;

    private fxchScrollableApi: FxchScrollableApi | null = null;
    private animationInterval: NodeJS.Timeout | null = null;

    private moveToHorizontal = 0;
    private movingToRightBorder = true;

    public ngAfterViewInit(): void {
        this.enableAutoScroll();
    }

    public ngOnDestroy(): void {
        this.disableAutoScroll();
    }

    public onScrollableInitialized(fxchScrollableApi: FxchScrollableApi): void {
        this.fxchScrollableApi = fxchScrollableApi;
    }

    public enableAutoScroll(): void {
        this.disableAutoScroll();

        const currentTransform = this.fxchScrollableApi?.getCurrentTransform();

        if (currentTransform == null) {
            return;
        }

        this.movingToRightBorder = currentTransform.horizontal! < this.moveToHorizontal;
        this.moveToHorizontal = currentTransform.horizontal!;

        this.animationInterval = setInterval(() => {

            if (Math.abs(this.moveToHorizontal) === Math.abs(currentTransform.maxHorizontal!)) {
                this.movingToRightBorder = false;
            } else if (Math.abs(this.moveToHorizontal) === 0) {
                this.movingToRightBorder = true;
            }

            if (this.movingToRightBorder) {
                this.moveToHorizontal -= 1;
            } else {
                this.moveToHorizontal += 1;
            }

            this.fxchScrollableApi?.moveToTransform({
                horizontal: this.moveToHorizontal,
                vertical: 0
            });

        });

    }

    public disableAutoScroll(): void {
        if (this.animationInterval != null) {
            clearInterval(this.animationInterval);
        }
    }

}