import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, HostListener, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { Subject, takeUntil } from 'rxjs';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';
import { FxchIsMobileService } from '@app/core/services/fxch-is-mobile.service';

@Component({
    selector: 'horizontal-text',
    standalone: true,
    imports: [
        CommonModule,
        TranslateModule,
    ],
    templateUrl: './horizontal-text.component.html',
    styleUrl: './horizontal-text.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HorizontalTextComponent {

    @ViewChild('textBlock') public textBlockElement: ElementRef<HTMLElement> | null = null;

    public transformWidth = 0;

    private readonly unsubscribe$: Subject<void> = new Subject<void>();

    constructor(
        private readonly elementRef: ElementRef<HTMLElement>,
        private readonly fxchScrollPositionService: FxchScrollingService,
        private readonly changeDetectorRef: ChangeDetectorRef,
    ) { }

    public ngOnInit(): void {
        this.fxchScrollPositionService.scroll$
            .pipe(takeUntil(this.unsubscribe$))
            .subscribe(() => this.calculateTransform());
    }

    public ngAfterViewInit(): void {
        this.calculateTransform();
    }

    @HostListener('window:resize')
    public onResize(): void {
        this.calculateTransform();
    }

    private calculateTransform(): void {

        const rect = this.elementRef.nativeElement.getBoundingClientRect();

        if (this.textBlockElement == null || rect.top > -200 || rect.bottom < (window.innerHeight + 200)) {
            return;
        }

        const relationAdjustment = FxchIsMobileService.any() ? 300 : 450;
        const relation = (this.textBlockElement.nativeElement.offsetWidth - window.innerWidth + relationAdjustment) / (rect.height - window.innerHeight);

        this.transformWidth = (rect.top + 200) * relation;

        this.changeDetectorRef.detectChanges();

    }

}