import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';
import { TranslateModule } from '@ngx-translate/core';
import { Subject, takeUntil } from 'rxjs';

@Component({
    selector: 'benefits',
    standalone: true,
    imports: [
        TranslateModule
    ],
    templateUrl: './benefits.component.html',
    styleUrl: './benefits.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BenefitsComponent implements OnInit, OnDestroy {

    @ViewChild('firstInfo') public readonly firstInfoElement: ElementRef<HTMLElement> | null = null;
    @ViewChild('secondInfo') public readonly secondInfoElement: ElementRef<HTMLElement> | null = null;
    @ViewChild('thirdInfo') public readonly thirdInfoElement: ElementRef<HTMLElement> | null = null;

    public static FirstInfoOffset = 200;
    public static SecondInfoOffset = 400;
    public static ThirdInfoOffset = 400;

    public static FirstInfoAminationDelayMs = 300;
    public static SecondInfoAminationDelayMs = 300;
    public static ThirdInfoAminationDelayMs = 300;

    public isFirstBenefitVisible = false;
    public isFirstBenefitActive = false;

    public isSecondBenefitVisible = false;
    public isSecondBenefitActive = false;

    public isThirdBenefitVisible = false;
    public isThirdBenefitActive = false;

    private readonly unsubscribe$: Subject<void> = new Subject<void>();

    constructor(
        private readonly fxchScrollingService: FxchScrollingService,
        private readonly changeDetectorRef: ChangeDetectorRef,
    ) { }

    public ngOnInit(): void {
        this.fxchScrollingService.scroll$
            .pipe(takeUntil(this.unsubscribe$))
            .subscribe(() => this.calculateClasses());
    }

    public ngOnDestroy(): void {
        this.unsubscribe$.next();
        this.unsubscribe$.complete();
    }

    private calculateClasses(): void {
        if (
            this.firstInfoElement == null ||
            this.secondInfoElement == null ||
            this.thirdInfoElement == null
        ) {
            return;
        }

        const rect = this.firstInfoElement.nativeElement.getBoundingClientRect();
        if (rect.top <= 0) {
            this.isFirstBenefitVisible = true;
        }

        const isFirstBlocVisible = this.isElementReachedTop(this.firstInfoElement.nativeElement, BenefitsComponent.FirstInfoOffset);
        const isSecondBlocVisible = this.isElementReachedTop(this.secondInfoElement.nativeElement, BenefitsComponent.SecondInfoOffset);
        const isThirdBlocVisible = this.isElementReachedTop(this.thirdInfoElement.nativeElement, BenefitsComponent.ThirdInfoOffset);

        this.setClasses(
            isFirstBlocVisible && !isSecondBlocVisible && !isThirdBlocVisible,
            isSecondBlocVisible && !isThirdBlocVisible,
            isThirdBlocVisible
        );

        this.changeDetectorRef.detectChanges();

    }

    private isElementReachedTop(element: HTMLElement, targetTop: number): boolean {
        return element.getBoundingClientRect().top <= targetTop;
    }

    private setClasses(isFirstACtive: boolean, isSecondActive: boolean, isThirdActive: boolean): void {

        this.isFirstBenefitVisible = isFirstACtive;
        this.isSecondBenefitVisible = isSecondActive;
        this.isThirdBenefitVisible = isThirdActive;

        setTimeout(() => {
            this.isFirstBenefitActive = isFirstACtive;
            this.changeDetectorRef.detectChanges();
        }, BenefitsComponent.FirstInfoAminationDelayMs);

        setTimeout(() => {
            this.isSecondBenefitActive = isSecondActive;
            this.changeDetectorRef.detectChanges();
        }, BenefitsComponent.SecondInfoAminationDelayMs);

        setTimeout(() => {
            this.isThirdBenefitActive = isThirdActive;
            this.changeDetectorRef.detectChanges();
        }, BenefitsComponent.ThirdInfoAminationDelayMs);

    }

}