import { ComponentType } from '@angular/cdk/portal';
import { Directive, ElementRef, EventEmitter, HostListener, Input, Output, Renderer2, TemplateRef } from '@angular/core';
import { take } from 'rxjs';

import { FxchPopupService } from '../../services/popup/fxch-popup.service';
import { DialogRef } from '@angular/cdk/dialog';

@Directive({
    selector: '[fxchPopup]',
    standalone: true
})
export class FxchPopupDirective {

    @Input('fxchPopup') public templateOrComponentRef!: TemplateRef<unknown> | ComponentType<unknown>;
    @Input() public hasBackdrop?: boolean;
    @Input() public popupCustomClass?: string;
    @Input() public popupData?: unknown;

    @Output() public opened: EventEmitter<DialogRef> = new EventEmitter<DialogRef>();

    private static readonly activeClass = 'fxch-active';

    constructor(
        private readonly popupService: FxchPopupService,
        private readonly elementRef: ElementRef<HTMLElement>,
        private readonly renderer: Renderer2
    ) { }

    @HostListener('click')
    public openPopup(): void {
        this.addActiveClass();

        const dialogRef = this.popupService.openPopup(
            this.templateOrComponentRef,
            this.elementRef,
            this.popupData,
            {
                hasBackdrop: this.hasBackdrop,
                customClass: this.popupCustomClass
            });

        dialogRef.closed
            .pipe(take(1))
            .subscribe(() => this.removeActiveClass());

        this.opened.emit(dialogRef);
    }

    private addActiveClass(): void {
        this.renderer.addClass(this.firstChild(), FxchPopupDirective.activeClass);
    }

    private removeActiveClass(): void {
        this.renderer.removeClass(this.firstChild(), FxchPopupDirective.activeClass);
    }

    private firstChild(): Element | null {
        return this.elementRef.nativeElement.firstElementChild;
    }
}