import { Injectable } from '@angular/core';

import { FxchScrollableTransformCoordinates } from '../models/fxch-scrollable-transform-coordinates';
import { FxchScrollableTransformData } from '../models/fxch-scrollable-transform-data';

@Injectable({ providedIn:'root' })
export class FxchScrollableTransformService {

    public getTransform(element: HTMLElement): FxchScrollableTransformCoordinates {
        const transform = window.getComputedStyle(element, null).getPropertyValue('-webkit-transform');
        const results = transform.match(/matrix(?:(3d)\(-{0,1}\d+(?:, -{0,1}\d+)*(?:, (-{0,1}\d+))(?:, (-{0,1}\d+))(?:, (-{0,1}\d+)), -{0,1}\d+\)|\(-{0,1}\d+(?:, -{0,1}\d+)*(?:, (-{0,1}\d+))(?:, (-{0,1}\d+))\))/);

        if (!results) {
            return { x: 0, y: 0 };
        }

        if (results[1] === '3d') {
            return { x: Number(results[2]), y: Number(results[3]) };
        }

        results.push('0');
        return { x: Number(results[5]), y: Number(results[6]) };
    }

    public createHorizontalTransformData(newHorizontal: number = 0, maxHorizontal: number = 0, alignByBorder: boolean = true): FxchScrollableTransformData {
        return this.createTransformData(newHorizontal, maxHorizontal, 0, 0, alignByBorder);
    }

    public createVerticalTransformData(newVertical: number = 0, maxVertical: number = 0, alignByBorder: boolean = true): FxchScrollableTransformData {
        return this.createTransformData(0, 0, newVertical, maxVertical, alignByBorder);
    }

    private createTransformData(
        newHorizontal: number,
        maxHorizontal: number,
        newVertical: number,
        maxVertical: number,
        alignByBorder: boolean): FxchScrollableTransformData {
        return {
            newHorizontal: newHorizontal,
            maxHorizontal: maxHorizontal,
            newVertical: newVertical,
            maxVertical: maxVertical,
            alignByBorder: alignByBorder
        };
    }
}