import { AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, ViewChild } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';

import { PromoComponent } from '@app/promo/promo.component';
import { AboutComponent } from '@app/about/about.component';
import { BenefitsComponent } from '@app/benefits/benefits.component';
import { PartnersComponent } from '@app/partners/partners.component';
import { TeamComponent } from '@app/team/team.component';
import { NewsComponent } from '@app/news/news.component';
import { TechnologyComponent } from '@app/technology/technology.component';
import { HorizontalTextComponent } from '@app/horizontal-text/horizontal-text.component';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';
import { NavigationAnchors } from '@app/navigation/navigation-anchors.enum';

@Component({
    selector: 'home',
    standalone: true,
    imports: [
        TranslateModule,
        PromoComponent,
        AboutComponent,
        TechnologyComponent,
        HorizontalTextComponent,
        BenefitsComponent,
        PartnersComponent,
        TeamComponent,
        NewsComponent,
    ],
    templateUrl: './home.component.html',
    styleUrl: './home.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HomeComponent implements AfterViewInit {

    @ViewChild('aboutUs') public aboutUsElement: ElementRef<HTMLElement> | null = null;
    @ViewChild('howItWorks') public howItWorksElement: ElementRef<HTMLElement> | null = null;
    @ViewChild('team') public teamElement: ElementRef<HTMLElement> | null = null;

    constructor(
        private readonly fxchScrollingService: FxchScrollingService,
    ) { }

    public ngAfterViewInit(): void {

        this.fxchScrollingService.setScrollAnchor(NavigationAnchors.Home, document.body);

        if (this.aboutUsElement != null) {
            this.fxchScrollingService.setScrollAnchor(NavigationAnchors.AboutUs, this.aboutUsElement.nativeElement);
        }

        if (this.howItWorksElement != null) {
            this.fxchScrollingService.setScrollAnchor(NavigationAnchors.HowItWorks, this.howItWorksElement.nativeElement);
        }

        if (this.teamElement != null) {
            this.fxchScrollingService.setScrollAnchor(NavigationAnchors.Team, this.teamElement.nativeElement);
        }

    }

}