import { ChangeDetectionStrategy, Component } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';

import { FxchButtonComponent } from '@core/controls/fxch-button/fxch-button.component';
import { FxchIconComponent } from '@app/core/controls/fxch-icon/fxch-icon.component';
import { FxchLogoComponent } from '@app/core/controls/fxch-logo/fxch-logo.component';
import { ContactModalService } from '@app/contact-modal/contact-modal.service';

@Component({
    selector: 'promo',
    standalone: true,
    imports: [
        TranslateModule,
        FxchLogoComponent,
        FxchButtonComponent,
        FxchIconComponent
    ],
    templateUrl: './promo.component.html',
    styleUrl: './promo.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PromoComponent {

    constructor(private readonly contactModalService: ContactModalService) { }

    public openContacts(): void {
        this.contactModalService.openContacts();
    }

}