import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, OnDestroy, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';
import { FxchButtonComponent } from '@app/core/controls/fxch-button/fxch-button.component';
import { Subject, takeUntil } from 'rxjs';
import { ContactModalService } from '@app/contact-modal/contact-modal.service';

@Component({
    selector: 'technology',
    standalone: true,
    imports: [
        CommonModule,
        TranslateModule,
        FxchButtonComponent
    ],
    templateUrl: './technology.component.html',
    styleUrl: './technology.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TechnologyComponent implements OnInit, AfterViewInit, OnDestroy {

    public isEndSection: boolean = false;

    private readonly unsubscribe$: Subject<void> = new Subject<void>();

    constructor(
        private readonly elementRef: ElementRef<HTMLElement>,
        private readonly fxchScrollingService: FxchScrollingService,
        private readonly changeDetectorRef: ChangeDetectorRef,
        private readonly contactModalService: ContactModalService,
    ) { }

    public ngOnInit(): void {
        this.fxchScrollingService.scroll$
            .pipe(takeUntil(this.unsubscribe$))
            .subscribe(() => this.detectSection());
    }

    public ngAfterViewInit(): void {
        this.detectSection();
    }

    public ngOnDestroy(): void {
        this.unsubscribe$.next();
        this.unsubscribe$.complete();
    }

    @HostListener('window:resize')
    public onResize(): void {
        this.detectSection();
    }

    public openContacts(): void {
        this.contactModalService.openContacts();
    }

    private detectSection(): void {
        const rect = this.elementRef.nativeElement.getBoundingClientRect();

        this.isEndSection = rect.top <= -(rect.height / 3.5);

        this.changeDetectorRef.detectChanges();
    }

}