import { AfterContentChecked, ContentChildren, Directive, ElementRef, EventEmitter, Input, OnDestroy, Output, QueryList } from '@angular/core';

import { FxchScrollableApi, FxchScrollableMoveToBorderOptions, FxchScrollableMoveToOptions, FxchScrollableMoveToTransformOptions } from './models/fxch-scrollable-api';
import { FxchScrollableState } from './models/fxch-scrollable-state';
import { FxchScrollableTransform } from './models/fxch-scrollable-transform';
import { FxchScrollableTransformData } from './models/fxch-scrollable-transform-data';
import { FxchScrollableTransformSettings } from './models/fxch-scrollable-transform-settings';
import { FxchScrollableContainerDirective } from './fxch-scrollable-container.directive';
import { FxchScrollableHorizontalItemDirective } from './fxch-scrollable-horizontal-item.directive';
import { FxchScrollableTargetDirective } from './fxch-scrollable-target.directive';
import { FxchScrollableVerticalItemDirective } from './fxch-scrollable-vertical-item.directive';
import { FxchScrollableHorizontalService } from './services/fxch-scrollable-horizontal.service';
import { FxchScrollableTransformService } from './services/fxch-scrollable-transform.service';
import { FxchScrollableVerticalService } from './services/fxch-scrollable-vertical.service';
import { FxchResizeObserverService } from '@core-services/fxch-resize-observer.service';

@Directive({
    selector: '[fxchScrollable]',
    standalone: true,
})
export class FxchScrollableDirective implements AfterContentChecked, OnDestroy {

    @Input() public horizontal?: boolean = false;
    @Input() public vertical?: boolean = true;

    @Input() public scrollItemsGap?: number = 0;
    @Input() public scrollItemsGapsSum?: number = 0;

    @Input() public isDragScrollEnabled?: boolean = true;
    @Input() public borderAdjustmentGap?: number = 15;

    @Input() public horizontalTransformSettings?: FxchScrollableTransformSettings;
    @Input() public verticalTransformSettings?: FxchScrollableTransformSettings;

    @Output() public onInitialized = new EventEmitter<FxchScrollableApi>();
    @Output() public onChanged = new EventEmitter<FxchScrollableTransform>();

    @ContentChildren(FxchScrollableContainerDirective, { descendants: true })
    public scrollableContainers!: QueryList<FxchScrollableContainerDirective>;

    public scrollableContainersArray!: Array<HTMLElement>;

    @ContentChildren(FxchScrollableTargetDirective, { descendants: true })
    public scrollableTargets!: QueryList<FxchScrollableTargetDirective>;

    public scrollableTargetsArray!: Array<HTMLElement>;

    @ContentChildren(FxchScrollableHorizontalItemDirective, { descendants: true })
    public horizontalScrollableItems?: QueryList<FxchScrollableHorizontalItemDirective>;

    public horizontalScrollableItemsArray!: Array<HTMLElement>;

    @ContentChildren(FxchScrollableVerticalItemDirective, { descendants: true })
    public verticalScrollableItems?: QueryList<FxchScrollableVerticalItemDirective>;

    public verticalScrollableItemsArray!: Array<HTMLElement>;

    public scrollable: HTMLElement;

    public scrollableState: FxchScrollableState = {
        startVerticalTransform: 0,
        startHorizontalTransform: 0,
        isDragCalculationEnabled: false,
        dragStartPositionEvent: null
    };

    public isDraggingForApi: boolean = false;

    private resizeObserver?: ResizeObserver;

    constructor(
        readonly scrollableRef: ElementRef<HTMLElement>,
        private readonly fxchScrollableHorizontalService: FxchScrollableHorizontalService,
        private readonly fxchScrollableVerticalService: FxchScrollableVerticalService,
        private readonly fxchScrollableTransformService: FxchScrollableTransformService,
        private readonly fxchResizeObserverService: FxchResizeObserverService
    ) {
        this.scrollable = scrollableRef.nativeElement;
    }

    public ngAfterContentChecked(): void {
        if (this.scrollableContainers == null || this.scrollableContainers.length === 0) {
            throw new Error('FxchScrollableDirective cannot find FxchScrollableContainerDirective.');
        }

        if (this.scrollableTargets == null || this.scrollableTargets.length === 0) {
            throw new Error('FxchScrollableDirective cannot find FxchScrollableTargetDirective.');
        }

        if (this.horizontal && (this.horizontalScrollableItems == null || this.horizontalScrollableItems.length === 0)) {
            throw new Error('FxchScrollableDirective cannot find FxchScrollableContainerDirective child fxchScrollableHorizontalItem elements.');
        }

        if (this.vertical && (this.verticalScrollableItems == null || this.verticalScrollableItems.length === 0)) {
            throw new Error('FxchScrollableDirective cannot find FxchScrollableContainerDirective child fxchScrollableVerticalItem elements.');
        }

        this.scrollableContainersArray = this.scrollableContainers.map(c => c.element);
        this.scrollableTargetsArray = this.scrollableTargets.map(t => t.element);
        this.horizontalScrollableItemsArray = this.horizontalScrollableItems?.map(i => i.element) || [];
        this.verticalScrollableItemsArray = this.verticalScrollableItems?.map(i => i.element) || [];

        this.resizeObserver = this.fxchResizeObserverService.create(() => this.adjustBorders());
        this.resizeObserver.observe(document.body);

        this.onInitialized.emit({
            moveTo: (options) => this.onIndexMove(options),
            moveToTransform: (options) => this.onTransformMove(options),
            moveToBorder: (options) => this.moveToBorder(options),
            getIndexTransformItemData: (options) => this.fxchScrollableHorizontalService.getIndexTransformItemData(
                options.horizontalItemIndex || 0,
                this.scrollItemsGapsSum || 0,
                this.scrollItemsGap || 0,
                this.horizontalScrollableItemsArray
            ),
            getCurrentTransform: () => this.getCurrentTransform(),
            scrollable: () => this.scrollable,
            scrollableContainers: () => this.scrollableContainersArray,
            scrollableTargets: () => this.scrollableTargetsArray,
            isDragging: () => this.isDraggingForApi
        });
    }

    public ngOnDestroy(): void {
        if (this.resizeObserver != null) {
            this.resizeObserver.disconnect();
        }
    }

    private adjustBorders(): void {
        const currentTransform = this.getCurrentTransform();
        const horizontal = Math.abs(currentTransform.horizontal || 0);
        const maxHorizontal = Math.abs(currentTransform.maxHorizontal || 0);
        const vertical = Math.abs(currentTransform.vertical || 0);
        const maxVertical = Math.abs(currentTransform.maxVertical || 0);

        const adjustOptions: FxchScrollableMoveToBorderOptions = {
            horizontalStart: false,
            horizontalEnd: horizontal > maxHorizontal,
            verticalStart: false,
            verticalEnd: vertical > maxVertical
        };

        if (adjustOptions.horizontalEnd || adjustOptions.verticalEnd) {
            this.moveToBorder(adjustOptions);
        } else {
            const resultTransform: FxchScrollableTransform = {
                horizontal: horizontal,
                maxHorizontal: maxHorizontal,
                vertical: vertical,
                maxVertical: maxVertical
            };

            this.postTransform(resultTransform);
            this.onChanged.emit(resultTransform);
        }
    }

    private onIndexMove(options: FxchScrollableMoveToOptions): void {
        if (!this.horizontal && !this.vertical) {
            return;
        }

        const transformationDataX = this.fxchScrollableHorizontalService.calculateIndexTransform(
            options.horizontalItemIndex,
            this.scrollableContainersArray[0],
            this.scrollItemsGapsSum || 0,
            this.scrollItemsGap || 0,
            this.horizontalScrollableItemsArray
        );

        const transformationDataY = this.fxchScrollableVerticalService.calculateIndexTransform(
            options.verticalItemIndex,
            this.scrollable,
            this.scrollableContainersArray[0],
            this.scrollItemsGapsSum || 0,
            this.scrollItemsGap || 0,
            this.verticalScrollableItemsArray
        );

        const resultTransform: FxchScrollableTransform = {};

        if (transformationDataX != null) {
            const resultHorizontalTransform = this.fxchScrollableHorizontalService.trySetTransform(
                transformationDataX,
                this.scrollableState,
                this.scrollableTargetsArray,
                this.borderAdjustmentGap || 0
            );

            if (resultHorizontalTransform != null) {
                resultTransform.horizontal = resultHorizontalTransform.horizontal;
                resultTransform.maxHorizontal = resultHorizontalTransform.maxHorizontal;
            }
        }

        if (transformationDataY != null) {
            const resultVerticalTransform = this.fxchScrollableVerticalService.trySetTransform(
                transformationDataY,
                this.scrollableState,
                this.scrollableTargetsArray,
                this.borderAdjustmentGap || 0
            );

            if (resultVerticalTransform != null) {
                resultTransform.vertical = resultVerticalTransform.vertical;
                resultTransform.maxVertical = resultVerticalTransform.maxVertical;
            }
        }

        this.postTransform(resultTransform);
        this.onChanged.emit(resultTransform);
    }

    private onTransformMove(options: FxchScrollableMoveToTransformOptions): void {
        if (!this.horizontal && !this.vertical) {
            return;
        }

        const currentTransform = this.getCurrentTransform();
        const transformationDataX = this.fxchScrollableTransformService.createHorizontalTransformData(options.horizontal, currentTransform.maxHorizontal);
        const transformationDataY = this.fxchScrollableTransformService.createVerticalTransformData(options.vertical, currentTransform.maxVertical);

        const resultTransform: FxchScrollableTransform = {};

        const resultHorizontalTransform = this.fxchScrollableHorizontalService.trySetTransform(
            transformationDataX,
            this.scrollableState,
            this.scrollableTargetsArray,
            this.borderAdjustmentGap || 0
        );

        if (resultHorizontalTransform != null) {
            resultTransform.horizontal = resultHorizontalTransform.horizontal;
            resultTransform.maxHorizontal = resultHorizontalTransform.maxHorizontal;
        }

        const resultVerticalTransform = this.fxchScrollableVerticalService.trySetTransform(
            transformationDataY,
            this.scrollableState,
            this.scrollableTargetsArray,
            this.borderAdjustmentGap || 0
        );

        if (resultVerticalTransform != null) {
            resultTransform.vertical = resultVerticalTransform.vertical;
            resultTransform.maxVertical = resultVerticalTransform.maxVertical;
        }

        this.postTransform(resultTransform);
        this.onChanged.emit(resultTransform);
    }

    private moveToBorder(options: FxchScrollableMoveToBorderOptions): void {
        if (!this.horizontal && !this.vertical) {
            return;
        }

        let transformationDataX: FxchScrollableTransformData | null = null;
        let transformationDataY: FxchScrollableTransformData | null = null;

        if (options.horizontalStart) {
            transformationDataX = this.fxchScrollableHorizontalService.calculateStartTransform(
                this.scrollableContainersArray[0],
                this.scrollItemsGapsSum || 0,
                this.scrollItemsGap || 0,
                this.horizontalScrollableItemsArray
            );
        } else if (options.horizontalEnd) {
            transformationDataX = this.fxchScrollableHorizontalService.calculateEndTransform(
                this.scrollableContainersArray[0],
                this.scrollItemsGapsSum || 0,
                this.scrollItemsGap || 0,
                this.horizontalScrollableItemsArray
            );
        }

        if (options.verticalStart) {
            transformationDataY = this.fxchScrollableVerticalService.calculateStartTransform(
                this.scrollable,
                this.scrollableContainersArray[0]
            );
        } else if (options.verticalEnd) {
            transformationDataY = this.fxchScrollableVerticalService.calculateEndTransform(
                this.scrollable,
                this.scrollableContainersArray[0]
            );
        }

        const resultTransform: FxchScrollableTransform = {};

        if (transformationDataX != null) {
            const resultHorizontalTransform = this.fxchScrollableHorizontalService.trySetTransform(
                transformationDataX,
                this.scrollableState,
                this.scrollableTargetsArray,
                this.borderAdjustmentGap || 0
            );

            if (resultHorizontalTransform != null) {
                resultTransform.horizontal = resultHorizontalTransform.horizontal;
                resultTransform.maxHorizontal = resultHorizontalTransform.maxHorizontal;
            }
        }

        if (transformationDataY != null) {
            const resultVerticalTransform = this.fxchScrollableVerticalService.trySetTransform(
                transformationDataY,
                this.scrollableState,
                this.scrollableTargetsArray,
                this.borderAdjustmentGap || 0
            );

            if (resultVerticalTransform != null) {
                resultTransform.vertical = resultVerticalTransform.vertical;
                resultTransform.maxVertical = resultVerticalTransform.maxVertical;
            }
        }

        this.postTransform(resultTransform);
        this.onChanged.emit(resultTransform);
    }

    private getCurrentTransform(): FxchScrollableTransform {
        const currentTransform = this.fxchScrollableTransformService.getTransform(this.scrollableTargetsArray[0]);
        const horizontalMaxTransform = this.fxchScrollableHorizontalService.getMaxTransform(
            this.scrollableContainersArray[0],
            this.scrollItemsGapsSum || 0,
            this.scrollItemsGap || 0,
            this.horizontalScrollableItemsArray
        );

        const verticalMaxTransform = this.fxchScrollableVerticalService.getMaxTransform(
            this.scrollable,
            this.scrollableContainersArray[0]
        );

        return {
            horizontal: currentTransform.x,
            maxHorizontal: horizontalMaxTransform,
            vertical: currentTransform.y,
            maxVertical: verticalMaxTransform
        };
    }

    public postTransform(transform: FxchScrollableTransform): void {
        if (this.vertical && this.verticalTransformSettings != null) {
            this.tryAddBorderClasses(transform, this.verticalTransformSettings, this.hasReachedTopBorder, this.hasReachedBottomBorder);
        }

        if (this.horizontal && this.horizontalTransformSettings != null) {
            this.tryAddBorderClasses(transform, this.horizontalTransformSettings, this.hasReachedLeftBorder, this.hasReachedRightBorder);
        }
    }

    private tryAddBorderClasses(
            transform: FxchScrollableTransform,
            settings: FxchScrollableTransformSettings,
            hasReachedStart: (transform: FxchScrollableTransform) => boolean,
            hasRechedEnd: (transform: FxchScrollableTransform) => boolean): void
        {
        if (settings.startBorderReachedClass) {
            if (hasReachedStart(transform)) {
                this.scrollable.classList.remove(settings.startBorderReachedClass);
            } else {
                this.scrollable.classList.add(settings.startBorderReachedClass);
            }
        }

        if (settings.endBorderReachedClass) {
            if (hasRechedEnd(transform)) {
                this.scrollable.classList.remove(settings.endBorderReachedClass);
            } else {
                this.scrollable.classList.add(settings.endBorderReachedClass);
            }
        }
    }

    private hasReachedLeftBorder(transform: FxchScrollableTransform): boolean {
        return transform.horizontal === 0;
    }

    private hasReachedRightBorder(transform: FxchScrollableTransform): boolean {
        if (transform.horizontal == null || transform.maxHorizontal == null) {
            return false;
        }
        return Math.abs(transform.horizontal) === Math.abs(transform.maxHorizontal);
    }

    private hasReachedTopBorder(transform: FxchScrollableTransform): boolean {
        return transform.vertical === 0;
    }

    private hasReachedBottomBorder(transform: FxchScrollableTransform): boolean {
        if (transform.vertical == null || transform.maxVertical == null) {
            return false;
        }
        return Math.abs(transform.vertical) === Math.abs(transform.maxVertical);
    }
}