import { Directive, ElementRef, Host, HostListener } from '@angular/core';

import { FxchScrollableTransform } from './models/fxch-scrollable-transform';
import { FxchScrollableDirective } from './fxch-scrollable.directive';
import { FxchScrollableHorizontalService } from './services/fxch-scrollable-horizontal.service';
import { FxchScrollableTransformService } from './services/fxch-scrollable-transform.service';
import { FxchScrollableVerticalService } from './services/fxch-scrollable-vertical.service';

@Directive({ 
    selector: '[fxchScrollableContainer]',
    standalone: true,
})
export class FxchScrollableContainerDirective {

    public element: HTMLElement;

    constructor(
        @Host() private readonly fxchScrollable: FxchScrollableDirective,
        private readonly elementRef: ElementRef<HTMLElement>,
        private readonly fxchScrollableHorizontalService: FxchScrollableHorizontalService,
        private readonly fxchScrollableVerticalService: FxchScrollableVerticalService,
        private readonly fxchScrollableTransformService: FxchScrollableTransformService
    ) {
        this.element = elementRef.nativeElement;
    }

    @HostListener('mousewheel', ['$event'])
    public onMouseWheel(event: WheelEvent): void {
        const isVerticalCalculation = this.fxchScrollableVerticalService.isVerticalScrollCalculation(
            this.fxchScrollable.vertical || false,
            event
        );

        const resultTransform: FxchScrollableTransform = {};

        if (isVerticalCalculation) {
            this.stopEvents(event);

            const transformationDataY = this.fxchScrollableVerticalService.calculateWheelTransform(
                event,
                this.fxchScrollable.scrollable,
                this.element,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray
            );

            const resultVerticalTransform = this.fxchScrollableVerticalService.trySetTransform(
                transformationDataY,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray,
                this.fxchScrollable.borderAdjustmentGap || 0
            );

            if (resultVerticalTransform != null) {
                resultTransform.vertical = resultVerticalTransform.vertical;
                resultTransform.maxVertical = resultVerticalTransform.maxVertical;
            }
        }

        const isHorizontalCalculation = this.fxchScrollableHorizontalService.isHorizontalScrollCalculation(
            this.fxchScrollable.horizontal || false,
            event
        );

        if (isHorizontalCalculation) {
            this.stopEvents(event);

            const transformationDataX = this.fxchScrollableHorizontalService.calculateWheelTransform(
                event,
                this.element,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray,
                this.fxchScrollable.scrollItemsGapsSum || 0,
                this.fxchScrollable.scrollItemsGapsSum || 0,
                this.fxchScrollable.horizontalScrollableItemsArray
            );

            const resultHorizontalTransform = this.fxchScrollableHorizontalService.trySetTransform(
                transformationDataX,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray,
                this.fxchScrollable.borderAdjustmentGap || 0
            );

            if (resultHorizontalTransform != null) {
                resultTransform.horizontal = resultHorizontalTransform.horizontal;
                resultTransform.maxHorizontal = resultHorizontalTransform.maxHorizontal;
            }
        }

        this.fxchScrollable.postTransform(resultTransform);
        this.fxchScrollable.onChanged.emit(resultTransform);
    }

    @HostListener('mousedown', ['$event'])
    @HostListener('touchstart', ['$event'])
    public onTouchStart(event: MouseEvent | TouchEvent): void {
        if (!this.fxchScrollable.isDragScrollEnabled) {
            return;
        }

        this.fxchScrollable.scrollableState.isDragCalculationEnabled = true;
        this.fxchScrollable.scrollableState.dragStartPositionEvent = event;

        const currentTransform = this.fxchScrollableTransformService.getTransform(this.fxchScrollable.scrollableTargetsArray[0]);
        this.fxchScrollable.scrollableState.startHorizontalTransform = currentTransform.x;
        this.fxchScrollable.scrollableState.startVerticalTransform = currentTransform.y;
    }

    @HostListener('mouseup')
    @HostListener('mouseleave')
    @HostListener('touchend')
    @HostListener('touchcancel')
    public onTouchEnd(): void {
        if (!this.fxchScrollable.isDragScrollEnabled) {
            return;
        }

        this.fxchScrollable.scrollableState.isDragCalculationEnabled = false;
        // Hack for click validation when scrollable items are clickable.
        setTimeout(() => this.fxchScrollable.isDraggingForApi = false);
    }

    @HostListener('mousemove', ['$event'])
    @HostListener('touchmove', ['$event'])
    public onTouchMove(event: MouseEvent | TouchEvent): void {
        if (!this.fxchScrollable.isDragScrollEnabled || !this.fxchScrollable.scrollableState.isDragCalculationEnabled) {
            return;
        }

        this.stopEvents(event);

        const resultTransform: FxchScrollableTransform = {};

        if (this.fxchScrollable.horizontal) {
            const transformationDataX = this.fxchScrollableHorizontalService.calculateTransform(
                event,
                this.element,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollItemsGapsSum || 0,
                this.fxchScrollable.scrollItemsGap || 0,
                this.fxchScrollable.horizontalScrollableItemsArray
            );

            const resultHorizontalTransform = this.fxchScrollableHorizontalService.trySetTransform(
                transformationDataX,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray,
                this.fxchScrollable.borderAdjustmentGap || 0
            );

            if (resultHorizontalTransform != null) {
                resultTransform.horizontal = resultHorizontalTransform.horizontal;
                resultTransform.maxHorizontal = resultHorizontalTransform.maxHorizontal;

                if (resultHorizontalTransform.horizontal !== this.fxchScrollable.scrollableState.startHorizontalTransform) {
                    this.fxchScrollable.isDraggingForApi = true;
                }
            }
        }

        if (this.fxchScrollable.vertical) {
            const transformationDataY = this.fxchScrollableVerticalService.calculateTransform(
                event,
                this.fxchScrollable.scrollable,
                this.element,
                this.fxchScrollable.scrollableState
            );

            const resultVerticalTransform = this.fxchScrollableVerticalService.trySetTransform(
                transformationDataY,
                this.fxchScrollable.scrollableState,
                this.fxchScrollable.scrollableTargetsArray,
                this.fxchScrollable.borderAdjustmentGap || 0
            );

            if (resultVerticalTransform != null) {
                resultTransform.vertical = resultVerticalTransform.vertical;
                resultTransform.maxVertical = resultVerticalTransform.maxVertical;

                if (resultVerticalTransform.vertical !== this.fxchScrollable.scrollableState.startVerticalTransform) {
                    this.fxchScrollable.isDraggingForApi = true;
                }
            }
        }

        this.fxchScrollable.postTransform(resultTransform);
        this.fxchScrollable.onChanged.emit(resultTransform);
    }

    private stopEvents(event: Event): void {
        event.stopPropagation();
        event.preventDefault();
    }
}