import { Injectable } from '@angular/core';

import { FxchScrollableTransformService } from './fxch-scrollable-transform.service';
import { FxchScrollableState } from '../models/fxch-scrollable-state';
import { FxchScrollableTransform } from '../models/fxch-scrollable-transform';
import { FxchScrollableTransformData } from '../models/fxch-scrollable-transform-data';

@Injectable({ providedIn:'root' })
export class FxchScrollableVerticalService {

    constructor(
        private readonly fxchScrollableTransformService: FxchScrollableTransformService
    ) {}

    public isVerticalScrollCalculation(vertical: boolean, event: WheelEvent): boolean {
        return vertical && !event.shiftKey && event.deltaY !== 0;
    }

    public calculateTransform(
        event: MouseEvent | TouchEvent,
        scrollable: HTMLElement,
        scrollableContainer: HTMLElement,
        scrollableState: FxchScrollableState): FxchScrollableTransformData
    {
        const newTransform = this.getNewEventTransform(event, scrollableState);
        const maxTransform = this.getMaxTransform(scrollable, scrollableContainer);
        return this.fxchScrollableTransformService.createVerticalTransformData(newTransform, maxTransform);
    }

    public calculateWheelTransform(
        event: WheelEvent,
        scrollable: HTMLElement,
        scrollableContainer: HTMLElement,
        scrollableState: FxchScrollableState,
        scrollableTargets: Array<HTMLElement>): FxchScrollableTransformData
    {
        const maxTransform = this.getMaxTransform(scrollable, scrollableContainer);

        const currentTransform = this.fxchScrollableTransformService.getTransform(scrollableTargets[0]);
        scrollableState.startVerticalTransform = currentTransform.y;

        const correctedDelta = -(event.deltaY >= -10 && event.deltaY <= 10 ? event.deltaY * 10 : event.deltaY);

        let newTransform = scrollableState.startVerticalTransform + correctedDelta;

        if (event.deltaY < 0 && scrollableState.startVerticalTransform <= 0 && scrollableState.startVerticalTransform >= -125) {
            newTransform = 0;
        } else if (event.deltaY > 0 &&
            Math.abs(maxTransform) - Math.abs(scrollableState.startVerticalTransform) <= 125 &&
            Math.abs(maxTransform) - Math.abs(scrollableState.startVerticalTransform) >= 0
        ) {
            newTransform = -maxTransform;
        }

        return this.fxchScrollableTransformService.createVerticalTransformData(
            newTransform > 0 ? -newTransform : newTransform,
            maxTransform);
    }

    public calculateIndexTransform(
        targetItemIndex: number,
        scrollable: HTMLElement,
        scrollableContainer: HTMLElement,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData | null
    {
        if (targetItemIndex < 0) {
            return null;
        }

        const newTransform = this.getNewIndexTransform(targetItemIndex, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        const maxTransform = this.getMaxTransform(scrollable, scrollableContainer);
        return this.fxchScrollableTransformService.createVerticalTransformData(newTransform, maxTransform);
    }

    public calculateStartTransform(scrollable: HTMLElement, scrollableContainer: HTMLElement): FxchScrollableTransformData {
        const maxTransform = this.getMaxTransform(scrollable, scrollableContainer);
        return this.fxchScrollableTransformService.createVerticalTransformData(0, maxTransform);
    }

    public calculateEndTransform(scrollable: HTMLElement, scrollableContainer: HTMLElement): FxchScrollableTransformData {
        const maxTransform = this.getMaxTransform(scrollable, scrollableContainer);
        return this.fxchScrollableTransformService.createVerticalTransformData(maxTransform, maxTransform);
    }

    public trySetTransform(
        transformationData: FxchScrollableTransformData,
        scrollableState: FxchScrollableState,
        scrollableTargets: Array<HTMLElement>,
        borderAdjustmentGap: number): FxchScrollableTransform | null
    {
        const currentTransform = this.fxchScrollableTransformService.getTransform(scrollableTargets[0]);

        if (this.hasNotReachedAnyBorder(transformationData) || !transformationData.alignByBorder) {
            // Move elements only if there is some space until the border

            if (this.shouldAdjustNearTopBorder(transformationData, scrollableState.startVerticalTransform, borderAdjustmentGap) && transformationData.alignByBorder) {
                // Transform to start to avoid wrong calculation when several px left
                return this.setTransform(0, transformationData, scrollableTargets);
            }

            if (this.shouldAdjustNearBottomBorder(transformationData, scrollableState.startVerticalTransform, borderAdjustmentGap) && transformationData.alignByBorder) {
                // Transform to end to avoid wrong calculation when several px left
                return this.setTransform(-transformationData.maxVertical, transformationData, scrollableTargets);
            }

            return this.setTransform(transformationData.newVertical, transformationData, scrollableTargets);
        } else if (
            transformationData.newVertical < 0 && Math.abs(transformationData.newVertical) > transformationData.maxVertical ||
            Math.abs(currentTransform.y) > transformationData.maxVertical
        ) {
            // Move elements to the bottom border in case elements disappeared or became thinner
            return this.setTransform(-transformationData.maxVertical, transformationData, scrollableTargets);
        } else if (transformationData.newVertical > 0) {
            return this.setTransform(0, transformationData, scrollableTargets);
        }

        return null;
    }

    public getMaxTransform(scrollable: HTMLElement, scrollableContainer: HTMLElement): number {
        let maxTransform = scrollableContainer.offsetHeight - scrollable.offsetHeight;

        if (maxTransform <= 0) {
            maxTransform = 0;
        }

        return maxTransform;
    }

    private getNewEventTransform(event: MouseEvent | TouchEvent, scrollableState: FxchScrollableState): number {
        if (event == null) {
            return 0;
        }

        const eventPostionY = event instanceof MouseEvent
                ? event.clientY
                : event.touches[0].clientY;

        const startEventPostionY = scrollableState.dragStartPositionEvent instanceof MouseEvent
            ? scrollableState.dragStartPositionEvent.clientY
            : scrollableState.dragStartPositionEvent?.touches[0].clientY;

        return scrollableState.startVerticalTransform + (eventPostionY - (startEventPostionY || 0));
    }

    private getNewIndexTransform(
        targetItemIndex: number,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): number
    {
        const gapsHeight = scrollItemsGapsSum > 0
            ? scrollItemsGapsSum
            : targetItemIndex * scrollItemsGap;

        const scrollableItemsHeightCalculationAction = (sum: number, element: HTMLElement, index: number): number => {
            return index < targetItemIndex
                ? sum + element.offsetHeight
                : sum;
        };

        const scrollableElementsHeightGapsWidth = scrollableItems.reduce(scrollableItemsHeightCalculationAction, 0) + gapsHeight;

        return -(scrollableElementsHeightGapsWidth);
    }

    private hasNotReachedAnyBorder(transformationData: FxchScrollableTransformData): boolean {
        return transformationData.newVertical <= 0 && transformationData.maxVertical >= 0
                && Math.abs(transformationData.newVertical) <= transformationData.maxVertical;
    }

    private shouldAdjustNearTopBorder(
        transformationData: FxchScrollableTransformData,
        startVerticalTransform: number,
        borderAdjustmentGap: number): boolean
    {
        return transformationData.newVertical >= -borderAdjustmentGap
            && startVerticalTransform !== 0
            && Math.abs(transformationData.maxVertical) > borderAdjustmentGap;
    }

    private shouldAdjustNearBottomBorder(
        transformationData: FxchScrollableTransformData,
        startVerticalTransform: number,
        borderAdjustmentGap: number): boolean
    {
        return (Math.abs(transformationData.maxVertical) - Math.abs(transformationData.newVertical))
            <= borderAdjustmentGap
            && startVerticalTransform !== 0
            && Math.abs(transformationData.maxVertical) > borderAdjustmentGap;
    }

    private setTransform(
        transform: number,
        transformationData: FxchScrollableTransformData,
        scrollableTargets: Array<HTMLElement>): FxchScrollableTransform | null
    {
        if (scrollableTargets != null && scrollableTargets.length > 0) {
            scrollableTargets.forEach(element => {
                const currentTransform = this.fxchScrollableTransformService.getTransform(element);
                element.style.transform = `translate3d(${currentTransform.x}px, ${Math.round(transform)}px, 0px)`;
            });

            return { vertical: Math.round(transform), maxVertical: transformationData.maxVertical };
        }

        return null;
    }
}