import { Injectable } from '@angular/core';

import { FxchScrollableTransformService } from './fxch-scrollable-transform.service';
import { FxchScrollableTrasform } from '../models/fxch-scrollable-api';
import { FxchScrollableState } from '../models/fxch-scrollable-state';
import { FxchScrollableTransform } from '../models/fxch-scrollable-transform';
import { FxchScrollableTransformData } from '../models/fxch-scrollable-transform-data';

@Injectable({ providedIn:'root' })
export class FxchScrollableHorizontalService {

    constructor(
        private readonly fxchScrollableTransformService: FxchScrollableTransformService
    ) { }

    public isHorizontalScrollCalculation(horizontal: boolean, event: WheelEvent): boolean {
        return horizontal && ((event.shiftKey && event.deltaY !== 0) || event.deltaX !== 0);
    }

    public calculateTransform(
        event: MouseEvent | TouchEvent,
        scrollableContainer: HTMLElement,
        scrollableState: FxchScrollableState,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData
    {
        const newTransform = this.getNewEventTransform(event, scrollableState);
        const maxTransform = this.getMaxTransform(scrollableContainer, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        return this.fxchScrollableTransformService.createHorizontalTransformData(newTransform, maxTransform);
    }

    public calculateWheelTransform(
        event: WheelEvent,
        scrollableContainer: HTMLElement,
        scrollableState: FxchScrollableState,
        scrollableTargets: Array<HTMLElement>,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData
    {
        const maxTransform = this.getMaxTransform(scrollableContainer, scrollItemsGapsSum, scrollItemsGap, scrollableItems);

        const currentTransform = this.fxchScrollableTransformService.getTransform(scrollableTargets[0]);
        scrollableState.startHorizontalTransform = currentTransform.x;

        let newTransform = 0;

        let isDeltaNegative = false;

        if (event.deltaX !== 0) {
            newTransform = scrollableState.startHorizontalTransform - event.deltaX;
            isDeltaNegative = event.deltaX < 0;
        } else {
            const correctedDelta = -(event.deltaY >= -10 && event.deltaY <= 10 ? event.deltaY * 10 : event.deltaY);
            newTransform = scrollableState.startHorizontalTransform + correctedDelta;
            isDeltaNegative = event.deltaY < 0;
        }

        if (isDeltaNegative && scrollableState.startHorizontalTransform <= 0 && scrollableState.startHorizontalTransform >= -125) {
            newTransform = 0;
        } else if (!isDeltaNegative &&
            Math.abs(maxTransform) - Math.abs(scrollableState.startHorizontalTransform) <= 125 &&
            Math.abs(maxTransform) - Math.abs(scrollableState.startHorizontalTransform) >= 0
        ) {
            newTransform = -maxTransform;
        }

        return this.fxchScrollableTransformService.createHorizontalTransformData(newTransform, maxTransform);
    }

    public calculateIndexTransform(
        targetItemIndex: number,
        scrollableContainer: HTMLElement,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData | null
    {
        if (targetItemIndex < 0) {
            return null;
        }

        const newTransform = this.getNewIndexTransform(targetItemIndex, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        const maxTransform = this.getMaxTransform(scrollableContainer, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        return this.fxchScrollableTransformService.createHorizontalTransformData(newTransform, maxTransform);
    }

    public calculateStartTransform(
        scrollableContainer: HTMLElement,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData
    {
        const maxTransform = this.getMaxTransform(scrollableContainer, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        return this.fxchScrollableTransformService.createHorizontalTransformData(0, maxTransform);
    }

    public calculateEndTransform(
        scrollableContainer: HTMLElement,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): FxchScrollableTransformData
    {
        const maxTransform = this.getMaxTransform(scrollableContainer, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        return this.fxchScrollableTransformService.createHorizontalTransformData(maxTransform, maxTransform);
    }

    public trySetTransform(
        transformationData: FxchScrollableTransformData,
        scrollableState: FxchScrollableState,
        scrollableTargets: Array<HTMLElement>,
        borderAdjustmentGap: number): FxchScrollableTransform | null
    {
        const currentTransform = this.fxchScrollableTransformService.getTransform(scrollableTargets[0]);

        if (this.hasNotReachedAnyBorder(transformationData) || !transformationData.alignByBorder) {
            // Move elements only if there is some space until the border

            if(this.shouldAdjustNearLeftBorder(transformationData, scrollableState.startHorizontalTransform, borderAdjustmentGap) && transformationData.alignByBorder) {
                // Transform to start to avoid wrong calculation when several px left
                return this.setTransform(0, transformationData, scrollableTargets);
            }

            if (this.shouldAdjustNearRightBorder(transformationData, scrollableState.startHorizontalTransform, borderAdjustmentGap) && transformationData.alignByBorder) {
                // Transform to end to avoid wrong calculation when several px left
                return this.setTransform(-transformationData.maxHorizontal, transformationData, scrollableTargets);
            }

            return this.setTransform(transformationData.newHorizontal, transformationData, scrollableTargets);
        } else if (
            transformationData.newHorizontal < 0 && Math.abs(transformationData.newHorizontal) > transformationData.maxHorizontal ||
            Math.abs(currentTransform.x) > transformationData.maxHorizontal
        ) {
            // Move elements to the right border in case elements disappeared or became thinner
            return this.setTransform(-transformationData.maxHorizontal, transformationData, scrollableTargets);
        } else if (transformationData.newHorizontal > 0) {
            return this.setTransform(0, transformationData, scrollableTargets);
        }

        return null;
    }

    public getIndexTransformItemData(
        targetItemIndex: number,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>
    ): FxchScrollableTrasform
    {
        const itemTransformValue = this.getNewIndexTransform(targetItemIndex, scrollItemsGapsSum, scrollItemsGap, scrollableItems);
        const itemWidth = scrollableItems[targetItemIndex].offsetWidth;
        return { transformValue: Math.abs(itemTransformValue), width: itemWidth };
    }

    /**
    * Calculates max X transform position value (positive).
    */
    public getMaxTransform(
        scrollableContainer: HTMLElement,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>): number
    {
        const gapsWidth = scrollItemsGapsSum > 0
            ? scrollItemsGapsSum
            : (scrollableItems.length - 1) * scrollItemsGap;

        const scrollableElementsWidth = scrollableItems
            .reduce((sum, element) => sum + element.offsetWidth, 0) + gapsWidth;

        // Subtract width of elements container so last slide would be on right side
        let maxTransform = scrollableElementsWidth - scrollableContainer.offsetWidth;

        if (maxTransform <= 0) {
            maxTransform = 0;
        }

        return maxTransform;
    }

    private getNewEventTransform(event: MouseEvent | TouchEvent, scrollableState: FxchScrollableState): number {
        if (event == null) {
            return 0;
        }

        const eventPositionX = event instanceof MouseEvent
            ? event.clientX
            : event.touches[0].clientX;

        const startEventPositonX = scrollableState.dragStartPositionEvent instanceof MouseEvent
            ? scrollableState.dragStartPositionEvent.clientX
            : scrollableState.dragStartPositionEvent?.touches[0].clientX;

        return scrollableState.startHorizontalTransform + (eventPositionX - (startEventPositonX || 0));
    }

    private getNewIndexTransform(
        targetItemIndex: number,
        scrollItemsGapsSum: number,
        scrollItemsGap: number,
        scrollableItems: Array<HTMLElement>
    ): number
    {
        const gapsWidth = scrollItemsGapsSum > 0
            ? scrollItemsGapsSum
            : targetItemIndex * scrollItemsGap;

        const scrollableItemsWidthCalculationAction = (sum: number, element: HTMLElement, index: number): number => {
            return index < targetItemIndex ? sum + element.offsetWidth : sum;
        };

        const scrollableElementsWithGapsWidth = scrollableItems.reduce(scrollableItemsWidthCalculationAction, 0) + gapsWidth;

        return -(scrollableElementsWithGapsWidth);
    }

    private hasNotReachedAnyBorder(transformationData: FxchScrollableTransformData): boolean {
        return transformationData.newHorizontal <= 0 && transformationData.maxHorizontal >= 0
            && Math.abs(transformationData.newHorizontal) <= transformationData.maxHorizontal;
    }

    private shouldAdjustNearLeftBorder(
        transformationData: FxchScrollableTransformData,
        startHorizontalTransform: number,
        borderAdjustmentGap: number): boolean
    {
        return transformationData.newHorizontal >= -borderAdjustmentGap
            && startHorizontalTransform !== 0
            && Math.abs(transformationData.maxHorizontal) > borderAdjustmentGap;
    }

    private shouldAdjustNearRightBorder(
        transformationData: FxchScrollableTransformData,
        startHorizontalTransform: number,
        borderAdjustmentGap: number): boolean
    {
        return (Math.abs(transformationData.maxHorizontal) - Math.abs(transformationData.newHorizontal))
            <= borderAdjustmentGap
            && startHorizontalTransform !== 0
            && Math.abs(transformationData.maxHorizontal) > borderAdjustmentGap;
    }

    private setTransform(
        transform: number,
        transformationData: FxchScrollableTransformData,
        scrollableTargets: Array<HTMLElement>): FxchScrollableTransform | null
    {
        if (scrollableTargets != null && scrollableTargets.length > 0) {
            scrollableTargets.forEach(element => {
                const currentTransform = this.fxchScrollableTransformService.getTransform(element);
                element.style.transform = `translate3d(${Math.round(transform)}px, ${currentTransform.y}px, 0px)`;
            });

            return { horizontal: Math.round(transform), maxHorizontal: transformationData.maxHorizontal };
        }

        return null;
    }
}