import { APP_INITIALIZER, ApplicationConfig, importProvidersFrom } from '@angular/core';
import { HttpClient, provideHttpClient } from '@angular/common/http';
import { provideRouter } from '@angular/router';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { initializeApplication } from '@core-layout/app/services/app.initializer';
import { AppService } from '@core-layout/app/services/app.service';
import { routes } from '@app/app.routes';
import { HammerModule } from '@angular/platform-browser';
import 'hammerjs';
import { ToastrModule } from 'ngx-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

export const appConfig: ApplicationConfig = {
    providers: [
        provideHttpClient(),
        importProvidersFrom(
            TranslateModule.forRoot({
                loader: {
                    provide: TranslateLoader,
                    useFactory: (httpClient: HttpClient) => new TranslateHttpLoader(httpClient, './assets/i18n/', '.json'),
                    deps: [HttpClient],
                },
            })
        ),
        { provide: APP_INITIALIZER, useFactory: initializeApplication, deps: [AppService], multi: true },
        provideRouter(routes),
        importProvidersFrom(HammerModule),
        importProvidersFrom(BrowserAnimationsModule),
        importProvidersFrom(ToastrModule.forRoot()),
    ]
};
