import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { first, second, third } from '@core-constants/about-card-types.constants';
import { FxchIconComponent } from '@app/core/controls/fxch-icon/fxch-icon.component';

@Component({
    selector: 'news-card',
    standalone: true,
    imports: [
        TranslateModule,
        CommonModule,
        FxchIconComponent
    ],
    templateUrl: './news-card.component.html',
    styleUrl: './news-card.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class NewsCardComponent {
    @Input() public newsTitle: string = '';
    @Input() public text: string = '';
    @Input() public type?:  first | second | third = 'first';

    public get aboutCardTypeClass(): string {
        return `fxch-news-card--${this.type}`;
    }
}
