import { Injectable } from '@angular/core';

import { FxchPopupService } from '@app/core/services/popup/fxch-popup.service';

@Injectable({ providedIn: 'root' })
export class ContactModalService {

    constructor(private readonly fxchPopupService: FxchPopupService) { }

    public openContacts(): void {
        void import('../contact-modal/contact-modal.component').then(({ ContactModalComponent }) => this.fxchPopupService.openDialog(ContactModalComponent, undefined, {customClass: 'fxch-contact-dialog'}));
    }

}