import { ChangeDetectionStrategy, ChangeDetectorRef, Component, HostListener, Inject, OnDestroy, OnInit } from '@angular/core';
import { CommonModule, DOCUMENT } from '@angular/common';
import { RouterModule } from '@angular/router';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { Languages } from '@core-enums/languages';
import { FxchScrollDetectorDirective } from '@core-directives/fxch-scroll-detector/fxch-scroll-detector.directive';
import { FxchPopupDirective } from '@core-directives/fxch-popup/fxch-popup.directive';
import { FxchButtonComponent } from '@core-controls/fxch-button/fxch-button.component';
import { FxchIconComponent } from '@core-controls/fxch-icon/fxch-icon.component';
import { FxchLogoComponent } from '@core-controls/fxch-logo/fxch-logo.component';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';
import { NavigationAnchors } from '@app/navigation/navigation-anchors.enum';
import { Subject, takeUntil } from 'rxjs';
import { DialogRef } from '@angular/cdk/dialog';
import { ContactModalService } from '@app/contact-modal/contact-modal.service';

@Component({
    selector: 'header',
    standalone: true,
    imports: [
        CommonModule,
        RouterModule,
        TranslateModule,
        FxchLogoComponent,
        FxchScrollDetectorDirective,
        FxchButtonComponent,
        FxchIconComponent,
        FxchPopupDirective,
    ],
    templateUrl: './header.component.html',
    styleUrl: './header.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HeaderComponent implements OnInit, OnDestroy {

    public readonly NavigationAnchors = NavigationAnchors;
    public readonly languages: Languages[] = Object.values(Languages);

    public readonly activeLinks: Record<string, boolean> = {};

    public theme: 'light' | 'dark' = 'light';

    private mobileMenuDialogRef: DialogRef | null = null;
    private desktopLanguagesMenuDialogRef: DialogRef | null = null;

    private readonly unsubscribe$: Subject<void> = new Subject<void>();

    constructor(
        @Inject(DOCUMENT) private readonly document: Document,
        private readonly changeDetectorRef: ChangeDetectorRef,
        private readonly translate: TranslateService,
        private readonly fxchScrollingService: FxchScrollingService,
        private readonly contactModalService: ContactModalService,
    ) { }

    public ngOnInit(): void {

        const storedTheme = localStorage.getItem('fxch-theme') as 'light' | 'dark' | null;

        if (storedTheme != null) {
            this.setTheme(storedTheme);
        }

        this.fxchScrollingService.scroll$
            .pipe(takeUntil(this.unsubscribe$))
            .subscribe(() => this.calculateActiveLinks());
    }

    public ngOnDestroy(): void {
        this.unsubscribe$.next();
        this.unsubscribe$.complete();
    }

    @HostListener('window:resize')
    public onResize(): void {
        this.calculateActiveLinks();
        this.closeMenus();
    }

    public onDesktopLanguagesMenuOpened(menuDialogRef: DialogRef): void {
        this.desktopLanguagesMenuDialogRef = menuDialogRef;
    }

    public onMobileMenuOpened(menuDialogRef: DialogRef): void {
        this.mobileMenuDialogRef = menuDialogRef;
    }

    public useLanguage(languages: Languages): void {
        this.translate.use(languages);

        this.document.documentElement.setAttribute('lang', languages);

        this.closeMenus();
    }

    public getCurrentLanguage() {
        return this.translate.currentLang;
    }

    public setTheme(theme: 'light' | 'dark'): void {
        this.theme = theme;

        if (this.theme === 'light') {
            this.document.documentElement.classList.remove('dark');
        } else {
            this.document.documentElement.classList.add('dark');
        }

        localStorage.setItem('fxch-theme', this.theme);
    }

    public scrollTo(id: string): void {
        this.closeMenus();
        this.fxchScrollingService.scrollTo(id);
    }

    public openContacts(): void {
        this.closeMenus();

        this.contactModalService.openContacts();
    }

    private calculateActiveLinks(): void {
        this.activeLinks[NavigationAnchors.AboutUs] = this.fxchScrollingService.isAnchorActive(NavigationAnchors.AboutUs);
        this.activeLinks[NavigationAnchors.HowItWorks] = this.fxchScrollingService.isAnchorActive(NavigationAnchors.HowItWorks);
        this.activeLinks[NavigationAnchors.Team] = this.fxchScrollingService.isAnchorActive(NavigationAnchors.Team);
        this.changeDetectorRef.detectChanges();
    }

    private closeMenus(): void {
        this.desktopLanguagesMenuDialogRef?.close();
        this.mobileMenuDialogRef?.close();
    }

}