import { OnChanges, Directive, ElementRef, Input, OnDestroy, Renderer2 } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';

import { SimpleChanges } from '@core-utils/simple-changes-generic';
import { FxchScrollDetectorClasses } from './fxch-scroll-detector-classes';
import { FxchResizeObserverService } from '@core-services/fxch-resize-observer.service';

@Directive({
    selector: '[fxchScrollDetector]',
    standalone: true,
})
export class FxchScrollDetectorDirective implements OnChanges, OnDestroy {

    @Input() public scrollableTraget?: ElementRef<HTMLElement>;
    @Input() public scrollClasses?: FxchScrollDetectorClasses;

    private resizeObserver?: ResizeObserver;
    private subscription?: Subscription;

    constructor(
        private readonly elementRef: ElementRef<HTMLElement>,
        private readonly renderer: Renderer2,
        private readonly resizeObserverService: FxchResizeObserverService
    ) { }


    public ngOnChanges(changes: SimpleChanges<FxchScrollDetectorDirective>): void {
        if (changes.scrollableTraget != null && changes.scrollableTraget.currentValue != null) {

            const scrollable = this.scrollableTraget
                ? this.scrollableTraget.nativeElement
                : this.elementRef.nativeElement;

            if (scrollable != null) {

                this.ngOnDestroy();

                this.subscription = fromEvent(scrollable, 'scroll').subscribe(() => this.onScroll(scrollable));

                this.resizeObserver = this.resizeObserverService.create(() => this.onScroll(scrollable));
                this.resizeObserver.observe(scrollable);

                this.onScroll(scrollable);
            }

        }
    }

    public ngOnDestroy(): void {
        if (this.subscription != null) {
            this.subscription.unsubscribe();
        }

        if (this.resizeObserver != null) {
            this.resizeObserver.disconnect();
        }
    }

    private onScroll(scrollable: HTMLElement): void {
        const isScrollToBottom = Math.ceil(scrollable.offsetHeight + scrollable.scrollTop) >= scrollable.scrollHeight;
        const isScrollToTop = scrollable.scrollTop === 0;

        this.toggleClass(isScrollToBottom, this.scrollClasses?.bottom);
        this.toggleClass(isScrollToTop, this.scrollClasses?.top);
    }

    private toggleClass(isRemoveClass: boolean, className?: string): void {
        if (!className) {
            return;
        }

        if (isRemoveClass) {
            this.renderer.removeClass(this.elementRef.nativeElement, className);
        } else {
            this.renderer.addClass(this.elementRef.nativeElement, className);
        }
    }
}