import { Dialog, DIALOG_DATA, DialogRef } from '@angular/cdk/dialog';
import { ConnectedPosition, Overlay, OverlayRef } from '@angular/cdk/overlay';
import { ComponentPortal, ComponentType } from '@angular/cdk/portal';
import { ElementRef, Injectable, Injector, NgZone, TemplateRef } from '@angular/core';
import { take } from 'rxjs';

import { OPENED_POPUP_CLASS } from './constants/fxch-popup.constants';
import { FxchPopupOptions } from './fxch-popup-options';
import { POPUP_POSITIONS } from '@core-constants/connected-positions.constants';

@Injectable({ providedIn: 'root' })
export class FxchPopupService {

    private readonly body = document.querySelector('body');

    constructor(
        private readonly injector: Injector,
        private readonly zone: NgZone,
        private readonly overlay: Overlay,
        private readonly dialog: Dialog
    ) { }

    public openDialog<T, R>(
        componentOrTemplate: ComponentType<T> | TemplateRef<T>,
        data?: R,
        options?: FxchPopupOptions
    ): DialogRef<unknown, T> {
        const dialogRef = this.createDialog({
            componentOrTemplate,
            hasBackdrop: options?.hasBackdrop,
            backdropClass: 'cdk-overlay-dark-backdrop',
            panelClass: options?.replacePanelClassBy ? options?.replacePanelClassBy : 'fxch-dialog',
            customClass: options?.customClass,
            data
        });

        return dialogRef;
    }

    public openPopup<T, R>(
        componentOrTemplate: ComponentType<T> | TemplateRef<T>,
        sourceElement: HTMLElement | ElementRef<HTMLElement>,
        data?: R | Partial<T> | null,
        options?: FxchPopupOptions
    ): DialogRef<unknown, T> {
        const dialogRef = this.createDialog({
            componentOrTemplate,
            hasBackdrop: options?.hasBackdrop,
            backdropClass: 'cdk-overlay-transparent-backdrop',
            panelClass: options?.replacePanelClassBy ? options?.replacePanelClassBy : 'fxch-dropdown',
            customClass: options?.customClass,
            data
        });

        // Places the popup in the correct position.
        this.updatePositionStrategy(dialogRef, sourceElement, options?.positions);
        // Fixes the popup size.
        setTimeout(() => this.updatePositionStrategy(dialogRef, sourceElement, options?.positions));

        return dialogRef;
    }

    public openPopupv2<T, R>(
        componentOrTemplate: ComponentType<T>,
        sourceElement: HTMLElement | ElementRef<HTMLElement>,
        data?: R | Partial<T> | null,
        options?: FxchPopupOptions
    ): OverlayRef {
        const overlayRef = this.overlay.create({
            hasBackdrop: true,
            disposeOnNavigation: true,
            scrollStrategy: this.overlay.scrollStrategies.noop(),
            backdropClass: 'cdk-overlay-transparent-backdrop',
            panelClass: options?.replacePanelClassBy ? options?.replacePanelClassBy : 'fxch-dropdown',
            positionStrategy: this.overlay
                .position()
                .flexibleConnectedTo(sourceElement)
                .withPositions(options?.positions ?? POPUP_POSITIONS)
                .withPush(true)
        });
        const localInjector = Injector.create({
            providers: [
                { provide: DIALOG_DATA, useValue: data }
            ],
            parent: this.injector
        });

        overlayRef.backdropClick()
            .pipe(take(1))
            .subscribe(() => overlayRef.detach());

        const portal = new ComponentPortal(componentOrTemplate, null, localInjector);

        overlayRef.attach(portal);

        return overlayRef;
    }

    public updatePositionStrategy<T>(
        dialogRef: DialogRef<unknown, T>,
        sourceElement: HTMLElement | ElementRef<HTMLElement>,
        positions?: ConnectedPosition[]
    ): void {
        dialogRef.overlayRef.updatePositionStrategy(
            this.overlay
                .position()
                .flexibleConnectedTo(sourceElement)
                .withPositions(positions ?? POPUP_POSITIONS)
                .withPush(true)
        );
    }

    private createDialog<T, R>(config: {
        componentOrTemplate: ComponentType<T> | TemplateRef<T>,
        hasBackdrop?: boolean,
        backdropClass: string,
        panelClass: string,
        customClass?: string | string[],
        data?: R
    }
    ): DialogRef<unknown, T> {
        return this.zone.run(() => {
            if (!this.body?.classList.contains(OPENED_POPUP_CLASS)) {
                this.body?.classList.add(OPENED_POPUP_CLASS);
            }

            const classes = config.customClass == null
                ? [config.panelClass]
                : typeof config.customClass === 'string'
                    ? [config.panelClass, config.customClass]
                    : [config.panelClass, ...config.customClass];

            const dialogRef = this.dialog.open(config.componentOrTemplate, {
                hasBackdrop: config.hasBackdrop != null ? config.hasBackdrop : true,
                backdropClass: config.backdropClass,
                panelClass: classes,
                autoFocus: false,
                data: config.data
            });

            dialogRef.closed.pipe(take(1)).subscribe(() => {
                if (this.dialog.openDialogs.length === 0) {
                    this.body?.classList.remove(OPENED_POPUP_CLASS);
                }
            });

            return dialogRef;
        });
    }
}