import { ChangeDetectionStrategy, Component, ElementRef, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import TeamData from 'assets/team/team.json';

import { FxchScrollDetectorDirective } from '@core-directives/fxch-scroll-detector/fxch-scroll-detector.directive';
import { FxchScrollableContainerDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-container.directive';
import { FxchScrollableHorizontalItemDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-horizontal-item.directive';
import { FxchScrollableDirective } from '@core-directives/fxch-scrollable/fxch-scrollable.directive';
import { FxchScrollableTargetDirective } from '@core-directives/fxch-scrollable/fxch-scrollable-target.directive';
import { FxchScrollableApi } from '@core-directives/fxch-scrollable/models/fxch-scrollable-api';
import { FxchButtonComponent } from '@core-controls/fxch-button/fxch-button.component';
import { TeamMemberOptions } from '@app/team-card/team-member-options';
import { TeamCardComponent } from '@app/team-card/team-card.component';

@Component({
    selector: 'team',
    standalone: true,
    imports: [
        CommonModule,
        TranslateModule,
        FxchScrollableTargetDirective,
        FxchScrollDetectorDirective,
        FxchScrollableContainerDirective,
        FxchScrollableHorizontalItemDirective,
        FxchScrollableDirective,
        FxchButtonComponent,
        TeamCardComponent,
    ],
    templateUrl: './team.component.html',
    styleUrl: './team.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TeamComponent {

    @ViewChild('teamBody') public teamBodyElement: ElementRef<HTMLElement> | null = null;
    @ViewChildren('teamCard') public teamCardElements: QueryList<ElementRef<HTMLElement>> | null = null;

    public readonly teamMembers: TeamMemberOptions[] = TeamData;

    public readonly columnGap = 20;

    private fxchScrollableApi: FxchScrollableApi | null = null;

    public onScrollableInitialized(fxchScrollableApi: FxchScrollableApi): void {
        this.fxchScrollableApi = fxchScrollableApi;
    }

    public showPrevious(): void {
        const moveToIndex = this.getPreviousIndex();

        if (moveToIndex != null) {
            this.fxchScrollableApi!.moveTo({ horizontalItemIndex: moveToIndex, verticalItemIndex: 0 });
        }
    }

    public showNext(): void {
        const moveToIndex = this.getNextIndex();

        if (moveToIndex != null) {
            this.fxchScrollableApi!.moveTo({ horizontalItemIndex: moveToIndex, verticalItemIndex: 0 });
        }
    }

    public isPreviousAvailable(): boolean {
        return this.getPreviousIndex() != null;
    }

    public isNextAvailable(): boolean {
        return this.getMoveIndex(
            () => 0,
            index => index < this.teamCardElements!.length,
            index => index + 1,
            (cardRect, bodyRect) => cardRect.right > bodyRect.right
        ) != null;
    }

    private getPreviousIndex(): number | null {
        return this.getMoveIndex(
            () => this.teamCardElements!.length - 1,
            index => index >= 0,
            index => index - 1,
            (cardRect, bodyRect) => cardRect.left < bodyRect.left,
        );
    }

    private getNextIndex(): number | null {
        return this.getMoveIndex(
            () => 0,
            index => index < this.teamCardElements!.length,
            index => index + 1,
            (cardRect, bodyRect) => cardRect.left > bodyRect.left
        );
    }

    private getMoveIndex(
        getInitailIndex: () => number,
        getLoopCondition: (index: number) => boolean,
        getUpdatedIndex: (index: number) => number,
        getScrollCondition: (cardRect: DOMRect, bodyRect: DOMRect) => boolean,
    ): number | null {
        if (this.fxchScrollableApi == null || this.teamCardElements == null || this.teamBodyElement == null) {
            return null;
        }

        const teamBodyRect = this.teamBodyElement.nativeElement.getBoundingClientRect();

        for (let index = getInitailIndex(); getLoopCondition(index); index = getUpdatedIndex(index)) {

            const teamCardElement = this.teamCardElements.get(index)!;
            const teamCardRect = teamCardElement.nativeElement.getBoundingClientRect();

            if (getScrollCondition(teamCardRect, teamBodyRect)) {
                return index;
            }

        }

        return null;
    }

}