import { Injectable } from '@angular/core';

import { FxchAnimationFrameService } from './fxch-animation-frame.service';

@Injectable({ providedIn: 'root' })
export class FxchResizeObserverService {

    constructor(
        private readonly fxchAnimationFrameService: FxchAnimationFrameService
    ) {}

    public create(callback: FrameRequestCallback): ResizeObserver {
        const animationContext = this.fxchAnimationFrameService.create(callback);
        const observer = new ResizeObserver(animationContext.request);
        return observer;
    }
}