import { ChangeDetectionStrategy, Component, ViewChild, ElementRef, HostListener, OnInit, AfterViewInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterOutlet } from '@angular/router';
import { TranslateModule, TranslateService } from '@ngx-translate/core';

import { FxchNotFoundPageService } from '@core-services/fxch-not-found-page.service';
import { FxchButtonComponent } from './core/controls/fxch-button/fxch-button.component';
import { HeaderComponent } from '@app/header/header.component';
import { Languages } from '@core-enums/languages';
import { FooterComponent } from './footer/footer.component';
import { FxchScrollingService } from '@app/core/services/fxch-scrolling.service';

@Component({
    selector: 'app',
    standalone: true,
    imports: [
        CommonModule,
        TranslateModule,
        RouterOutlet,
        FxchButtonComponent,
        HeaderComponent,
        FooterComponent,
    ],
    templateUrl: './app.component.html',
    styleUrl: './app.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AppComponent implements OnInit, AfterViewInit {

    @ViewChild('scrollContainer') public scrollContainerElement: ElementRef<HTMLElement> | null = null;

    public getScreenWidth = 2000;
    public getScreenHeight = 2000;

    public readonly isNotFoundPage$ = this.fxchNotFoundPageService.isNotFoundPage$;

    constructor(
        translate: TranslateService,
        private readonly fxchNotFoundPageService: FxchNotFoundPageService,
        private readonly fxchScrollPositionService: FxchScrollingService,
    ) {
        translate.setDefaultLang(Languages.EN);
        translate.use(Languages.EN);
    }

    public ngOnInit(): void {
        this.getScreenSize();
    }

    public ngAfterViewInit(): void {
        if (this.scrollContainerElement == null) {
            return;
        }

        this.fxchScrollPositionService.setScrollingElement(this.scrollContainerElement.nativeElement);
    }

    @HostListener('window:resize')
    public getScreenSize(): void {
        this.getScreenWidth = window.innerWidth;
        this.getScreenHeight = window.innerHeight;
    }

    public onScroll() {
        this.fxchScrollPositionService.scroll$.next();
    }

}