import { ConnectedPosition, ConnectionPositionPair } from '@angular/cdk/overlay';

export const POPUP_POSITIONS_OVERLAY_Y_TOP: ConnectedPosition[] = [
    {
        originX: 'start',
        originY: 'bottom',
        overlayX: 'start',
        overlayY: 'top'
    },
    {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'end',
        overlayY: 'top'
    }
];

export const POPUP_POSITIONS_OVERLAY_Y_BOTTOM: ConnectedPosition[] = [
    {
        originX: 'start',
        originY: 'top',
        overlayX: 'start',
        overlayY: 'bottom'
    },
    {
        originX: 'end',
        originY: 'top',
        overlayX: 'end',
        overlayY: 'bottom'
    }
];

export const POPUP_POSITIONS: ConnectedPosition[] = [
    ...POPUP_POSITIONS_OVERLAY_Y_TOP,
    ...POPUP_POSITIONS_OVERLAY_Y_BOTTOM
];

export const MENU_POSITIONS: ConnectionPositionPair[] = [
    new ConnectionPositionPair(
        { originX: 'end', originY: 'top' },
        { overlayX: 'start', overlayY: 'top' }
    ),
    new ConnectionPositionPair(
        { originX: 'start', originY: 'bottom' },
        { overlayX: 'start', overlayY: 'top' }
    ),
    new ConnectionPositionPair(
        { originX: 'end', originY: 'bottom' },
        { overlayX: 'end', overlayY: 'top' }
    ),
    new ConnectionPositionPair(
        { originX: 'start', originY: 'top' },
        { overlayX: 'start', overlayY: 'bottom' }
    ),
];