import { ChangeDetectionStrategy, Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ClipboardModule } from '@angular/cdk/clipboard';

import { FxchButtonComponent } from '@core/controls/fxch-button/fxch-button.component';
import { FxchIconComponent } from '@core/controls/fxch-icon/fxch-icon.component';
import { FxchLogoComponent } from '@app/core/controls/fxch-logo/fxch-logo.component';
import { FxchToastService } from '@app/core/toast/services/fxch-toast.service';

@Component({
    selector: 'footer',
    standalone: true,
    imports: [
        CommonModule,
        RouterModule,
        TranslateModule,
        ClipboardModule,
        FxchLogoComponent,
        FxchButtonComponent,
        FxchIconComponent,
    ],
    templateUrl: './footer.component.html',
    styleUrl: './footer.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class FooterComponent {

    constructor(private readonly fxchToastService: FxchToastService) { }

    public copied(isSuccesfull: boolean): void {

        if (!isSuccesfull) {
            this.fxchToastService.showError('FOOTER.NOT_COPIED');

            return;
        }

        this.fxchToastService.showSuccess('FOOTER.COPIED');

    }

}