import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Input, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';

import { first, second, third } from '@core-constants/about-card-types.constants';
import { FxchIsMobileService } from '@app/core/services/fxch-is-mobile.service';
import { Debounce } from '@app/core/decorators/debounce.decorator';

@Component({
    selector: 'about-card',
    standalone: true,
    imports: [
        TranslateModule,
        CommonModule,
    ],
    templateUrl: './about-card.component.html',
    styleUrl: './about-card.component.scss',
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AboutCardComponent {

    @Input() public aboutTitle: string = '';
    @Input() public aboutTitleSecondLine?: string = '';
    @Input() public text: string = '';
    @Input() public type?: first | second | third = 'first';

    @ViewChild('textInner') public textInner: ElementRef<HTMLElement> | null = null;

    public isHover: boolean = false;
    public timer: NodeJS.Timeout | null = null;

    private readonly defaultTruncateTimeMs = 500;

    public get aboutCardTypeClass(): string {
        return `fxch-about-card--${this.type}`;
    }

    public get isHoverClass(): string {
        if (this.isHover) {
            return `fxch-about-card--active`;
        } else {
            return '';
        }
    }

    constructor(
        private readonly changeDetectorRef: ChangeDetectorRef,
    ) { }

    public toggleTap(): void {
        if (!FxchIsMobileService.any()) {
            return;
        }

        this.isHover = !this.isHover;

        this.clearTime();
        this.setClasses();

        this.changeDetectorRef.detectChanges();
    }

    public toggleHover(): void {
        if (FxchIsMobileService.any()) {
            return;
        }

        this.isHover = !this.isHover;

        this.clearTime();
        this.setClasses();

        this.changeDetectorRef.detectChanges();
    }

    private setClasses(): void {
        if (this.isHover) {
            this.textInner?.nativeElement.classList.remove('fxch-about-card-body-text-inner--truncated');
            return;
        }

        this.timer = setTimeout(() => {
            this.clearTime();
            this.textInner?.nativeElement.classList.add('fxch-about-card-body-text-inner--truncated');
            this.changeDetectorRef.detectChanges();
        }, this.defaultTruncateTimeMs);
    }

    private clearTime(): void {
        if (this.timer != null) {
            clearTimeout(this.timer);
        }
    }

}