import { ElementRef, Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject, from, map, of } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class FxchScrollingService {

    private readonly scrollAnchors: Record<string, HTMLElement> = {};

    private scrollingElement: HTMLElement | null = null;

    public readonly scroll$: Subject<void> = new Subject<void>();

    public setScrollingElement(scrollingElement: HTMLElement): void {
        this.scrollingElement = scrollingElement;
    }

    public getScrollingElement(): HTMLElement | null {
        return this.scrollingElement;
    }

    public getScrollAnchor(id: string): HTMLElement {
        return this.scrollAnchors[id];
    }

    public setScrollAnchor(id: string, scrollingElement: HTMLElement): void {
        this.scrollAnchors[id] = scrollingElement;
    }

    public scrollTo(id: string): void {
        setTimeout(() => {
            if (this.scrollingElement == null || this.scrollAnchors[id] == null) {
                return;
            }

            const targetElement = this.scrollAnchors[id];

            this.scrollingElement.scroll({
                top: targetElement.offsetTop,
                left: targetElement.offsetLeft,
                behavior: 'smooth'
            });
        });
    }

    public isAnchorActive(id: string): boolean {
        if (this.scrollAnchors[id] == null) {
            return false;
        }

        const targetElement = this.scrollAnchors[id];
        const rect = targetElement.getBoundingClientRect();

        return rect.top <= 100 && rect.bottom >= 100;
    }

}